<script>
  // Import statements

import {
  currentUser,
  currentProfile,
  allUsers,
  allProfs,
  allSuperProfs,
  allAdmins,
  allProgs,
  altProfile,
  //profProfile,
  addingStatus,
  filteredUserArray,
  filteredProfArray,
  filteredAdminArray,
  filteredSuperProfArray,
  filteredProgArray
} from '../stores.js';

import AddUser from '../routes/AddUser.svelte';
import { fetchUsers, fetchProfs, fetchProgs, getAltProfile, getProfProfile, fetchProfProgs, fetchSuperProfs, fetchAdmins } from '../fileDbHandling.js';
import { signUp } from '../loginService.js';
import {
login,
pwd,
name,
surname,
phone,
email,
role,
gender,
dob,
address,
pocode
} from '../stores.js';



// Variables and stores

let toggle = false;
let profToggle = false;
let userArray = [];
let ProfArray = [];
let ProgArray = [];
let superProfArray = [];
let AdminArray =[];
let activeTab = 'users';
let activeWindow = 'users';
let searchTerm = "";
let profile = {
  name: " ",
}; 


let currentPage = 0;
const entriesPerPage = 50;
let startIdx,endIdx;
$: startIdx = currentPage * entriesPerPage;
$: endIdx = startIdx + entriesPerPage;

fresh();
// Functions

async function getProfs() {
  
  const unsubscribeProfs = allProfs.subscribe(profs => {
      ProfArray = [...profs];
      filteredProfArray.set(ProfArray);
    });
    allProfs.set([]);
  await getAllProfs();
  unsubscribeProfs();
  //console.log(ProfArray);

}

async function getProgs() {
  
  const unsubscribeProgs = allProgs.subscribe(progs => {
      ProgArray = [...progs];
      filteredProgArray.set(ProgArray);
    });
    allProgs.set([]);
  await getAllProgs();
  unsubscribeProgs();
  //console.log(ProfArray);

}



async function getSuperProfs() {
  
  const unsubscribeSuperProfs = allSuperProfs.subscribe(profs => {
      superProfArray = [...profs];
      filteredSuperProfArray.set(superProfArray);
    });
    filteredSuperProfArray.set([]);
  await getAllSuperProfs();
  unsubscribeSuperProfs();
  //console.log(ProfArray);

}

async function getAdmins() {
  
  const unsubscribeAdmins = allAdmins.subscribe(profs => {
      AdminArray = [...profs];
      filteredAdminArray.set(AdminArray);
    });
    allAdmins.set([]);
  await getAllAdmins();
  unsubscribeAdmins();
  //console.log(ProfArray);

}

async function getProfProgs() {
  await fetchProfProgs();
  
  
}

async function getUsers() {
  const unsubscribeUsers = allUsers.subscribe(users => {
      userArray = [...users];
      filteredUserArray.set(userArray);
    });
    allUsers.set([]);
  await getAllUsers();
  
 
  unsubscribeUsers();
}

function getProf(id) {
  profProfile.set(findProfById(id));
  toggleProf();
}

function findProfById(id) {
  return ProfArray.find(prof => prof.id === id) || null;
}

function findUserById(id) {
  return userArray.find(user => user.id === id) || null;
}
function findProfessionalById(id) {
  return ProfArray.find(user => user.id === id) || null;
}


async function getAllUsers() {
  await fetchUsers();
  //console.log("Fetched all users");
}

async function getAllProfs() {
  await fetchProfs();
  //console.log("Fetched all professionals");
}
async function getAllProgs() {
  await fetchProgs();
  //console.log("Fetched all professionals");
}


async function getAllSuperProfs() {
  await fetchSuperProfs();
  //console.log("Fetched all professionals");
}

async function getAllAdmins() {
  await fetchAdmins();
  //console.log("Fetched all professionals");
}


function openUser(id) {
  console.log(id);
  addingStatus.set(2);
  altProfile.set(findUserById(id));
}

function openProfessional(id) {
  console.log("opening prof");
  addingStatus.set(3);
  altProfile.set(findProfessionalById(id));
}

function switchTab(tab) {
  activeTab = tab;
}
function switchWindow(window) {
  activeWindow = window;
  handleSearch({target: {value: ''}});
  //console.log(window);
}

async function refresh(){
    
    
    const popup = document.getElementById('loadingPopup');
      //console.log("opening popup 1");
      popup.style.display = 'block';
      if(activeWindow == 'users'){
        if(activeTab == 'users'){            
            await getUsers();
        }else if(activeTab == 'professionals'){
          await getProfs();
        }else if(activeTab == 'superProfessionals'){
          await getSuperProfs();
        }else if(activeTab == 'admins'){
          await getAdmins();
        }

        
      }else if(activeWindow == 'programs'){
        await getProgs();
        
      }
      
      popup.style.display = 'none';
      

    

}
function delay(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}
async function fresh(){
    
    
    await getUsers();
    await getProfs();
    await getProgs();
    await getSuperProfs();
    await getAdmins();
    
}




function handleSearch(event) {
  
  searchTerm = event.target.value.toLowerCase();
  //console.log(searchTerm);
  filteredUserArray.set(
    userArray.filter(user => {
      return (
        user.name.toLowerCase().includes(searchTerm) ||
        user.surname.toLowerCase().includes(searchTerm) ||
        user.email.toLowerCase().includes(searchTerm) ||
        user.phone_number.toLowerCase().includes(searchTerm) ||
        user.date_of_birth.toLowerCase().includes(searchTerm)
      );
    })
  );

  filteredProfArray.set(
    ProfArray.filter(professional => {
      return (
        professional.name.toLowerCase().includes(searchTerm) ||
        professional.surname.toLowerCase().includes(searchTerm) ||
        professional.email.toLowerCase().includes(searchTerm) ||
        professional.phone_number.toLowerCase().includes(searchTerm) ||
        professional.date_of_birth.toLowerCase().includes(searchTerm)
      );
    })
  );

  filteredSuperProfArray.set(
    superProfArray.filter(professional => {
      return (
        professional.name.toLowerCase().includes(searchTerm) ||
        professional.surname.toLowerCase().includes(searchTerm) ||
        professional.email.toLowerCase().includes(searchTerm) ||
        professional.phone_number.toLowerCase().includes(searchTerm) ||
        professional.date_of_birth.toLowerCase().includes(searchTerm)
      );
    })
  );

  filteredAdminArray.set(
    AdminArray.filter(admin => {
      return (
        admin.name.toLowerCase().includes(searchTerm) ||
        admin.surname.toLowerCase().includes(searchTerm) ||
        admin.email.toLowerCase().includes(searchTerm)
      );
    })
  );

  filteredProgArray.set(
    ProgArray.filter(program => {
      return (
        program.name.toLowerCase().includes(searchTerm) ||
        program.user_surname.toLowerCase().includes(searchTerm) ||
        program.user_name.toLowerCase().includes(searchTerm) ||
        program.folder_name.toLowerCase().includes(searchTerm)
      );
    })
  );


}

// Main component


$: {
  // Update filteredUserArray when searchTerm or userArray changes
  filteredUserArray.set(
    userArray.filter(user => {
      return (
        user.name.toLowerCase().includes(searchTerm) ||
        user.surname.toLowerCase().includes(searchTerm) //||
        //user.email.toLowerCase().includes(searchTerm) ||
        //user.phone_number.toLowerCase().includes(searchTerm) ||
        //user.date_of_birth.toLowerCase().includes(searchTerm)
      );
    })
  );
}


$: {
  // Update filteredUserArray when searchTerm or userArray changes
  filteredProfArray.set(
    ProfArray.filter(professional => {
      return (
        professional.name.toLowerCase().includes(searchTerm) ||
        professional.surname.toLowerCase().includes(searchTerm) //||
        //user.email.toLowerCase().includes(searchTerm) ||
        //user.phone_number.toLowerCase().includes(searchTerm) ||
        //user.date_of_birth.toLowerCase().includes(searchTerm)
      );
    })
  );
}
function nextPage() {
    currentPage += 1;
  }

  // Create a function to handle navigation to the previous section
  function previousPage() {
    currentPage -= 1;
  }


async function onSignUp() {
        // Sign up functionality
        await signUp();
        
    }

    function closeForm(){
      const popup = document.getElementById('addUserPopup');
      //console.log("opening popup 1");
      popup.style.display = 'none';
    }
    function openForm(){
      const popup = document.getElementById('addUserPopup');
      //console.log("opening popup 1");
      popup.style.display = 'block';
    }


</script>

<main>
  <div class = "super-container">
    <div class = "side-bar">
      <div class="user-details">
          
         
      </div>
      <div class="side-bar-buttons">
        <button class="{activeWindow === 'profile' ? 'active' : ''}" on:click={() => switchWindow('profile')}>
          <i class="fa-solid fa-id-badge"></i>
            <p>Details</p>
        </button>
        <button class="{activeWindow === 'users' ? 'active' : ''}" on:click={() => switchWindow('users')}>
          <i class="fa-solid fa-users"></i>
          <p>Users</p>
        </button>
        <button class="{activeWindow === 'programs' ? 'active' : ''}" on:click={() => switchWindow('programs')}>
          <i class="fa-solid fa-file-waveform"></i>
          <p>Programs</p>
        </button>
      </div>
      
           
    </div>

      <div class="lower-container">
        
          
        {#if activeWindow === 'profile'}
            
      <div class = "profile-window">
        <div class="user-details">
          <div class="user-info">
            <h3 class="profilename">{$currentProfile.name} {$currentProfile.surname}</h3>
            <p class="profile-role">{$currentUser.user_role}</p>
            <div class="card-content">
              <div class="info-row">
                  <div class="info-label">Email Address:</div>
                  <div class="info-value">{$currentProfile.email}</div>
              </div>
              <div class="info-row">
                  <div class="info-label">Date of Birth:</div>
                  <div class="info-value">{$currentProfile.date_of_birth}</div>
              </div>
              <div class="info-row">
                  <div class="info-label">Phone Number:</div>
                  <div class="info-value">{$currentProfile.phone_number}</div>
              </div>
              <div class="info-row">
                  <div class="info-label">Gender:</div>
                  <div class="info-value">{$currentProfile.gender}</div>
              </div>
              <div class="info-row">
                <div class="info-label">Created:</div>
                <div class="info-value">{$currentProfile.created_at}</div>
              </div>
              <div class="info-row">
                <div class="info-label">Last updated:</div>
                <div class="info-value">{$currentProfile.updated_at}</div>
            </div>

            <div class="info-row">
              <div class="info-label">Address:</div>
              <div class="info-value">{$currentProfile.address}</div>
          </div>
          </div>
          </div>
          <div class = "avatar-div">
            {#if $currentUser.user_role === "Administrator"}
              <i class="fa-solid fa-user-shield fa-2xl"></i>
            {:else if $currentUser.user_role === "Professional" || $currentUser.user_role === "S. Professional"}
              <i class="fa-solid fa-user-tie fa-2xl"></i>
            {:else}
              <i class="fa-solid fa-user fa-2xl"></i>
            {/if}
          </div>
        </div>
            
            
        </div>
        
    
        {/if}
          
          {#if activeWindow === 'users'}
          <div class="action-bar">
            <input type="text" id="searchInput" placeholder="Search..." on:input={handleSearch} />
            <button on:click={openForm} class="add-user-button">
              <i class="fas fa-user-plus"></i>
            </button>
            <button on:click={refresh} class="add-user-button">
              <i class="fas fa-rotate"></i>
            </button>
          </div>
          <div class="tab-selector">
              <button class="{activeTab === 'users' ? 'active' : ''}" on:click={() => switchTab('users')}>Users</button>
              <button class="{activeTab === 'professionals' ? 'active' : ''}" on:click={() => switchTab('professionals')}>Professionals</button>
              <button class="{activeTab === 'superProfessionals' ? 'active' : ''}" on:click={() => switchTab('superProfessionals')}>S. Professionals</button>
              <button class="{activeTab === 'admins' ? 'active' : ''}" on:click={() => switchTab('admins')}>Admins</button>
          
          </div>

          {#if activeTab === 'users'}
          <div class="user-list-container">
            <div class="file-list">
              
              <table class="user-table">
                <!-- Table header -->
                <thead class="sticky-header">
                  <tr>
                    <th class="table-heading">Name</th>
                    <th class="table-heading">Email</th>
                    <th class="table-heading">Phone Number</th>
                    <th class="table-heading">Professional</th>
                    <th class="table-heading">Date of Birth</th>
                  </tr>
                </thead>
          
                <!-- Table body -->
                <tbody>
                  {#each $filteredUserArray as user}
                  <tr class="clickable-row" on:click={() => openUser(user.id)}>
                    <td class="table-entry">{user.name} {user.surname}</td>
                    <td class="table-entry">{user.email}</td>
                    <td class="table-entry">{user.phone_number}</td>
                    <td class="table-entry">{user.professional}</td>
                    <td class="table-entry">{user.date_of_birth}</td>
                  </tr>
                  {/each}
                </tbody>
              </table>
            </div>
          </div>
          {/if}

          {#if activeTab === 'professionals'}
          <div class="user-list-container">
            <div class="file-list">
              <table class="user-table">
                <thead class="sticky-header">
                  <tr>
                    <th class="table-heading">Name</th>
                    <th class="table-heading">Email</th>
                    <th class="table-heading">Phone Number</th>
                    <th class="table-heading">Date of Birth</th>
                  </tr>
                </thead>
                <tbody>
                  {#each $filteredProfArray as professional}
                  <tr class="clickable-row" on:click={openProfessional(professional.id)}>
                    <td class="table-entry">{professional.name} {professional.surname}</td>
                    <td class="table-entry">{professional.email}</td>
                    <td class="table-entry">{professional.phone_number}</td>
                    <td class="table-entry">{professional.date_of_birth}</td>
                  </tr>
                  {/each}
                </tbody>
              </table>
            </div>
          </div>
          
          {/if}
          {#if activeTab === 'superProfessionals'}
          <div class="user-list-container">
            <div class="file-list">
              <table class="user-table">
                <thead class="sticky-header">
                  <tr>
                    <th class="table-heading">Name</th>
                    <th class="table-heading">Email</th>
                    <th class="table-heading">Phone Number</th>
                    <th class="table-heading">Date of Birth</th>
                  </tr>
                </thead>
                <tbody>
                  {#each $filteredSuperProfArray as professional}
                  <tr class="clickable-row" on:click={openProfessional(professional.id)}>
                    <td class="table-entry">{professional.name} {professional.surname}</td>
                    <td class="table-entry">{professional.email}</td>
                    <td class="table-entry">{professional.phone_number}</td>
                    <td class="table-entry">{professional.date_of_birth}</td>
                  </tr>
                  {/each}
                </tbody>
              </table>
            </div>
          </div>
          
          {/if}
          {#if activeTab === 'admins'}
          <div class="user-list-container">
            <div class="file-list">
              <table class="user-table">
                <thead class="sticky-header">
                  <tr>
                    <th class="table-heading">Name</th>
                    <th class="table-heading">Email</th>
                    <th class="table-heading">Phone Number</th>
                    <th class="table-heading">Date of Birth</th>
                  </tr>
                </thead>
                <tbody>
                  {#each $filteredAdminArray as admin}
                  <tr>
                    <td class="table-entry">{admin.name} {admin.surname}</td>
                    <td class="table-entry">{admin.email}</td>
                    <td class="table-entry">{admin.phone_number}</td>
                    <td class="table-entry">{admin.date_of_birth}</td>
                  </tr>
                  {/each}
                </tbody>
              </table>
            </div>
          </div>
          
          {/if}
          {/if}

          
          {#if activeWindow === 'programs'}
          <div class="action-bar">
            <input type="text" id="searchInput" placeholder="Search..." on:input={handleSearch} />
            <button on:click={refresh} class="add-user-button">
              <i class="fas fa-rotate"></i>
            </button>
          </div>
          
          <div class="user-list-container">
            <div class="file-list">
              <table class="program-table">
                <thead class="sticky-header">
                  <tr>
                    <th class="table-heading">File</th>
                    <th class="table-heading">Folder</th>
                    <th class="table-heading">User</th>
                    <th class="table-heading">Created</th>
                  </tr>
                </thead>
                <tbody>
                  {#each $filteredProgArray.slice(startIdx, endIdx) as program}
                  <!-- Display the limited entries based on the current page -->
                  <tr>
                    <td class="table-entry">{program.name}</td>
                    <td class="table-entry">{program.folder_name}</td>
                    <td class="table-entry">{program.user_name} {program.user_surname}</td>
                    <td class="table-entry">{program.created}</td>
                  </tr>
                  {/each}
                </tbody>
              </table>
            </div>
            
          </div>
          <div>
            {#if currentPage > 0}
              <button class="nav-button" on:click={previousPage}>Previous</button>
            {/if}
          
            {#if $filteredProgArray.length > endIdx}
              <button class="nav-button" on:click={nextPage}>Next</button>
            {/if}
          </div>
          
         
          {/if}
          
      </div>

      
  </div>

  <div id="addUserPopup" class="popup">    
      <div class="form-container">
        <h1>Add User/Professional</h1>
        <form on:submit|preventDefault={onSignUp}>
            <div class="form-columns">
                <div class="form-column">
                    <label>
                        Username:
                        <input type="text" bind:value={$login} required />
                    </label>
                    <label>
                        Password:
                        <input type="password" bind:value={$pwd} required />
                    </label>
                    <label>
                        Name:
                        <input type="text" bind:value={$name} required />
                    </label>
                    <label>
                        Surname:
                        <input type="text" bind:value={$surname} required />
                    </label>
                    <label>
                        Role:
                        <select bind:value={$role} required>
                            <option value="">Select Role</option>
                            <option value="ROLE_PROFESSIONAL">Professional</option>
                            <option value="ROLE_USER">User</option>
                        </select>
                    </label>
                </div>
                <div class="form-column">
                    <label>
                        Gender:
                        <select bind:value={$gender} required>
                            <option value="">Select Gender</option>
                            <option value="male">Male</option>
                            <option value="female">Female</option>
                        </select>
                    </label>
                    <label>
                        Date of Birth:
                        <input type="date" bind:value={$dob} required />
                    </label>
                    <label>
                        Email:
                        <input type="email" bind:value={$email} required />
                    </label>
                    <label>
                        Phone Number:
                        <input type="tel" bind:value={$phone} required />
                    </label>
                    <label>
                        Address:
                        <input type="text" bind:value={$address} required />
                    </label>
                    <label>
                        Postal Code:
                        <input type="number" bind:value={$pocode} required />
                    </label>
                </div>
            </div>
            <button type="submit">Add</button>
        </form>
        <button on:click={closeForm}>Cancel</button>
    </div>
  </div>

  <div id="loadingPopup" class="popup">
    <div class="loading-circle">
      <i class="fa-solid fa-spinner fa-spin"></i>
    </div>
  </div>

   
</main>
   
        

      
 
  
<style>
  main {
    padding: 0px;
    margin: 0px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    height: 100%;
  }

  .super-container {
    display: flex;
    flex-direction: row;
    justify-content:flex-start;
    width: 100%;
    height: 100%;
  }

  .side-bar {
    width: 17%;
    overflow: auto;
    margin-left: 0;
    margin-right: 1%;
  }

  .side-bar button {
  padding: 10px 20px;
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  background-color: transparent;
  text-align: left;
  margin: 0;
  color: #333;
  border: none;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
  width: 80%;
}

.side-bar p {
  margin: 0;
  margin-left: 25px;
}

.side-bar-buttons {
  margin-top: 0px;
}



.side-bar button:hover {
  background-color: #eeeeee;
 
}

.side-bar button.active {
  background-color: #e6e6e6;
}

.profile-window {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: start;
}


  .user-details {
    display: flex;
    width: 90%;
    padding: 10px;
    flex-direction: row;
    align-items: start;
    margin-bottom: 10px;
    justify-content: space-between;
    padding: 10px;
  }

  .profilename {
    font-size: 24px;
    margin-bottom: 2px;
    margin-top: 12px;
  }

  .avatar-div {
    height: 100%;
    padding-top: 30px;
    font-size: 48px;
    color: #00b6a0;

  }

  .nav-button {
    margin: 20px;
    width: 120px;
    border-radius: 4px;
  }

  
  
.card-content {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  margin-left: 20px;
}

.info-row {
  display: flex;
  margin-bottom: 8px;
}

.info-label {
  font-weight: bold;
  margin-right: 8px;
}

.info-value {
  flex-grow: 1;
}



  .lower-container {
    display: flex;
    flex-direction: column;
    width: 73%;
    margin-top: 0px;
    padding-bottom: 10px;
    justify-content: space-between;
    align-items: center;
    overflow-x: auto;
  }

  .action-bar {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  width: 100%;
  padding: 10px;
  padding-left: 0;
  padding-right: 0;
}
.action-bar input[type="text"] {
  padding: 8px;
  flex: 1;
  margin-top: 12px;
  margin-right: 30px;
  border: none; /* Remove the border */
  border-bottom: 1px solid #ccc; /* Add a bottom border */
  border-radius: 0; /* Remove border-radius */
  font-size: 14px;
}

.action-bar input[type="text"]:focus {
  outline: none;
  border-color: #999;
  box-shadow: none; /* Remove the box-shadow */
}


  .tab-selector {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  width: 100%;
  padding: 10px;
  padding-left: 0;
  padding-right: 0;
}


.tab-selector button {
  padding: 10px 20px;
  background-color: transparent;
  text-decoration: underline;
  margin: 0;
  color: #333;
  border: none;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
  width: 25%;
}



.tab-selector button:hover {
  background-color: #eeeeee;
}

.tab-selector button.active {
  background-color: #e6e6e6;
}

.add-user-button {
    padding: 0;
    padding-left: 10px;
    padding-right: 10px;
    margin: 0;
    background-color: transparent;
    color: #000000;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-weight: bold;
    font-size: 13px;
    transition: background-color 0.3s ease;
  }

  .add-user-button:hover{
    color: #0056b3;
   
