import { get } from "svelte/store";
import { currentUser } from "./stores";
import { currentProfile } from "./stores";
import { login, pwd, name, surname, phone, email, role, gender, dob, address, pocode } from './stores.js';

export async function loginUser(username, password) {
  try {
    const formData = new URLSearchParams();
    formData.append('login', username);
    formData.append('pwd', password);

    const response = await fetch('https://inhealion.gr/api_2.php', {
      method: 'POST',
      body: formData
    });

    if (response.ok) {
      const data = await response.json();
      await getProfile(data.user_id);
      try {
        const roleMapping = {
          ROLE_ADMIN: 'Administrator',
          ROLE_PROFESSIONAL: 'Professional',
          ROLE_SUPER_PROFESSIONAL: 'S. Professional',
          ROLE_USER: 'User'
        };
      
        if (data.hasOwnProperty('user_role')) {
          const role = data.user_role;
          if (role in roleMapping) {
            data.user_role = roleMapping[role];
          } else {
            throw new Error(`Invalid user role: ${role}`);
          }
        } else {
          throw new Error('user_role not found in data.');
        }
      
        currentUser.set(data);
      
      } catch (error) {
        console.error('Error:', error.message);
        // Handle the error as per your requirements
      }

      
      

    } else {
      const error = await response.text();
      alert(error);
    }
  } catch (error) {
    alert("Couldn't log In, Please Check your credentials and internet connection");
    console.error('Login Error:', error);
    
  }
}

export function getProfile(id) {
  return new Promise(async (resolve, reject) => {
    const formData = new URLSearchParams();
    formData.append('fetchprofilebyid', id);

    try {
      const response = await fetch('https://inhealion.gr/api_2.php', {
        method: 'POST',
        body: formData
      });

      if (response.ok) {
        const data = await response.json();
        currentProfile.set(data);
        //console.log(data);

        resolve(data);
      } else {
        const error = await response.text();
        reject(new Error(error));
      }
    } catch (error) {
      console.error('Login Error:', error);
      reject(new Error('Error Fetching Profile'));
    }
  });
}



export async function logout(username, password) {
  try {
    const formData = new URLSearchParams();
    formData.append('logout', '0');
    
    const response = await fetch('https://inhealion.gr/api_2.php', {
      method: 'POST',
      body: formData
    });

    if (response.ok) {
      currentUser.set({"loggedIn": false});

    } else {
      const error = await response.text();
      alert(error);
    }
  } catch (error) {
    console.error('Logout Error:', error);
    alert('An error occurred during logout.');
  }
}

