<script>
    import { addingStatus, altProfile, files, programs, filteredUserArray, filteredProgArray, allUsers, profProgs} from "../stores";
    import { fetchFolders } from '../fileDbHandling.js';
    import { fetchPrograms } from '../fileDbHandling.js';
    import { fetchProfProgs } from "../fileDbHandling.js";
    import { updateProfessional } from "../fileDbHandling.js";
    import { fetchUsers } from "../fileDbHandling.js";
    import{uploadFile} from "../file-handler.js";
  
    

    let toggle = false;
    let searchTerm = "";
    let currentProfile;
    let folders = [];
    let programArray = [];
    let isProgramList = false;
    let user_id = 3;
    let activeTab = 'users';
    let userArray = [];
    let ProgArray = [];
    
    let selectedUser;






    const unsubscribeId = altProfile.subscribe(aId => {
        currentProfile = aId;
        user_id = aId.user_id; 
        //console.log(currentProfile);   
    });

    unsubscribeId();
    const unsubscribeProgs = profProgs.subscribe(progs => {
      ProgArray = progs.filter(prog => prog.professional_id === user_id);
      filteredProgArray.set(ProgArray);
    });
    unsubscribeProgs();

    const unsubscribeUsers = allUsers.subscribe(users => {
      userArray = users.filter(user => user.professional_id === user_id);
      filteredUserArray.set(userArray);
    });
    unsubscribeUsers();




    function goBack() {
        if(isProgramList){
            isProgramList = false;
        }
        else{
            addingStatus.set(0);
        }
    }

    function toggleDetails (){
      toggle = !toggle;
  }

  function handleSearch(event) {
  
  searchTerm = event.target.value.toLowerCase();
  //console.log(searchTerm);
  filteredUserArray.set(
    userArray.filter(user => {
      return (
        user.name.toLowerCase().includes(searchTerm) ||
        user.surname.toLowerCase().includes(searchTerm) ||
        user.email.toLowerCase().includes(searchTerm) ||
        user.phone_number.toLowerCase().includes(searchTerm) ||
        user.date_of_birth.toLowerCase().includes(searchTerm)
      );
    })
  );

  filteredProgArray.set(
    ProgArray.filter(program => {
      return (
        program.name.toLowerCase().includes(searchTerm)
                
      );
    })
  );
}
function switchTab(tab) {
  activeTab = tab;
}
function openUser(id) {
  console.log(id);
  addingStatus.set(2);
  altProfile.set(findUserById(id));
}

function openProfessional(id) {
  console.log("opening prof");
  addingStatus.set(3);
  altProfile.set(findProgramById(id));
}

function findUserById(id) {
  return userArray.find(user => user.id === id) || null;
}
function findProgramById(id) {
  return ProgArray.find(user => user.id === id) || null;
}

function openPopup(n) {
  console.log("opening popups");
  if( n == 1)
  {
    const popup = document.getElementById('uploadPopup');
    //console.log("opening popup 1");
    popup.style.display = 'block';
    const userIdInput = document.getElementById('userIdInput');
    userIdInput.value = currentProfile.user_id;
  }else if(n == 2) {
    const popup = document.getElementById('addProfPopup');
    //console.log("opening popup 2");
    popup.style.display = 'block';
    const userIdInput = document.getElementById('userIdInput2');
    userIdInput.value = currentProfile.user_id;

  }
  
  //console.log(currentProfile);
}

function closePopup(n) {
  if(n == 1){
    const popup = document.getElementById('uploadPopup');
    popup.style.display = 'none';
  }else if(n == 2){
    const popup = document.getElementById('addProfPopup');
    popup.style.display = 'none';
  }
  
}

async function uploadFileFromPopup() {
  const fileInput = document.getElementById('fileInput');
  const userIdInput = document.getElementById('userIdInput');
  const updateCheckbox = document.getElementById('updateCheckbox');

  const file = fileInput.files[0];
  const userId = userIdInput.value;
  const update = updateCheckbox.checked;

  if (file && userId) {
    await uploadFile(file, userId, update);
    await fetchFileHistory();
    closePopup(1);
  } else {
    console.error('Please select a file and provide a user ID.');
  }
}

async function updateProfessionalfromPopup() {
  try {
    
    console.log("Checking:");
    console.log(selectedUser);
    console.log(user_id);
    
    await updateProfessional(selectedUser, user_id);
    await fetchUsers();

    // Promise resolved successfully, show success alert
    const unsubscribeUsers = allUsers.subscribe(users => {
      userArray = users.filter(user => user.professional_id === user_id);
      filteredUserArray.set(userArray);
      console.log(userArray);
    });
    unsubscribeUsers();
    alert('Professional updated successfully');
    
    closePopup(2);
  } catch (error) {
    // Promise rejected or an error occurred, show error alert
    if (error.message === 'Professional not found') {
      alert('Professional not found');
    } else if (error.message === 'Internal server error') {
      alert('Internal server error');
    } else {
      alert('An unknown error occurred');
    }
  }
}
async function fetchFileHistory() {
    await fetchProfProgs();
    const unsubscribeProgs = profProgs.subscribe(progs => {
      ProgArray = progs.filter(prog => prog.professional_id === user_id);
      filteredProgArray.set(ProgArray);
    });
    unsubscribeProgs();
  }



    
</script>

<main>
    <div class="container">
        <div class="user-details">
            <div class="user-info">
              <h3 class="username">{currentProfile.name} {currentProfile.surname}</h3>
              <p class="user-role">Professional</p>
              <button class="detail-button" on:click={toggleDetails}>Details</button>
            </div>
            <div>
              <img src="avatar.png" alt="User Avatar" class="avatar" />
            </div>
          </div>
      
          {#if toggle}
            <div class="card">
              <div class="card-content">
                <div class="info-row">
                  <div class="info-label">Email Address:</div>
                  <div class="info-value">{currentProfile.email}</div>
                </div>
                <div class="info-row">
                  <div class="info-label">Date of Birth:</div>
                  <div class="info-value">{currentProfile.date_of_birth}</div>
                </div>
                <div class="info-row">
                  <div class="info-label">Phone Number:</div>
                  <div class="info-value">{currentProfile.phone_number}</div>
                </div>
                <div class="info-row">
                  <div class="info-label">Address:</div>
                  <div class="info-value">{currentProfile.address}</div>
                </div>
              </div>
            </div>
          {/if}
          <div class="lower-container">
    
      
            <div class="tab-selector">
                <input type="text" id="searchInput" placeholder="Search..." on:input={handleSearch} />
                <button class="{activeTab === 'users' ? 'active' : ''}" on:click={() => switchTab('users')}>Users</button>
                <button class="{activeTab === 'programs' ? 'active' : ''}" on:click={() => switchTab('programs')}>Programs</button>
            </div>
      
            {#if activeTab === 'users'}
            <div class="user-list-container">
              <div class="file-list">
                
                <table class="user-table">
                  <!-- Table header -->
                  <thead class="sticky-header">
                    <tr>
                      <th class="table-heading">Name</th>
                      <th class="table-heading">Email</th>
                      <th class="table-heading">Phone Number</th>
                      <th class="table-heading">Date of Birth</th>
                    </tr>
                  </thead>
            
                  <!-- Table body -->
                  <tbody>
                    {#each $filteredUserArray as user}
                    <tr class="clickable-row" on:click={() => openUser(user.id)}>
                      <td class="table-entry">{user.name} {user.surname}</td>
                      <td class="table-entry">{user.email}</td>
                      <td class="table-entry">{user.phone_number}</td>
                      <td class="table-entry">{user.date_of_birth}</td>
                    </tr>
                    {/each}
                  </tbody>
                </table>
              </div>
            </div>
            {/if}
      
            {#if activeTab === 'programs'}
            <div class="user-list-container">
              <div class="file-list">
                <table class="user-table">
                  <thead class="sticky-header">
                    <tr>
                      <th class="table-heading">Name</th>
                      <th class="table-heading">Created</th>
                      <th class="table-heading">Last Updated</th>
                    </tr>
                  </thead>
                  <tbody>
                    {#each $filteredProgArray as program}
                    <tr class="clickable-row" on:click={openProfessional(program.id)}>
                      <td class="table-entry">{program.name}</td>
                      <td class="table-entry">{program.created_at}</td>
                      <td class="table-entry">{program.updated_at}</td>
                    </tr>
                    {/each}
                  </tbody>
                </table>
              </div>
            </div>
            
            {/if}
        </div>


        <div class="button-container">
          <button on:click={goBack}>Go Back</button>
          <button on:click={() => openPopup(1)}>Upload Progam</button>
          <button on:click={() => openPopup(2)}>Add User</button>
        </div>
    </div>
    <div id="uploadPopup" class="popup">
      <div class="popup-content">
        <h2>Upload File</h2>
        <input type="file" id="fileInput">
        <input type="text" id="userIdInput" placeholder="User ID" readonly>
        <label>
          <input type="checkbox" id="updateCheckbox">
          Update existing file
        </label>
        <button on:click={uploadFileFromPopup}>Upload</button>
        <button on:click={()=> closePopup(1)}>Close</button>
      </div>
    </div>
    
    <div id="addProfPopup" class="popup2">
      <div class="popup-content">
        <h2>Add Professional</h2>
        <input type="text" id="userIdInput2" placeholder="User ID" readonly>
        <label for="updateDropdown">Select a User:</label>
        <select id="updateDropdown" bind:value={selectedUser}>
          {#each $allUsers as option}
            <option value={option.user_id}>{option.name} {option.surname}</option>
          {/each}
        </select>
        <button on:click={updateProfessionalfromPopup}>Add</button>
        <button on:click={() => closePopup(2)}>Close</button>
      </div>
    </div>
    
    


</main>

<style>
    main {
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 100vh;
        min-width: 100vw;
        margin: 0;
    }

    .container {
        min-height: 100vh;
        min-width: 100vw;
        margin: 0;
        background-color: #fff;
        padding: 40px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    
    button {
        margin-top: 10px;
        background-color: #007bff;
        color: #fff;
        padding: 12px;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        font-weight: bold;
        transition: background-color 0.3s ease;
    }

    button:hover {
        background-color: #0056b3;
    }

    .user-details {
    display: flex;
    width: 90%;
    padding: 10px;
    flex-direction: row;
    align-items: center;
    margin-bottom: 10px;
    justify-content: space-between;
    padding: 10px;
  }

  .username {
    font-size: 24px;
    margin-bottom: 2px;
  }

  .user-role {
    font-size: 18px;
    color: #555;
    margin: 3px 0px;
  }

  .avatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
  }

  .detail-button {
  padding: 5px;
  background-color: #f2f2f2;
  color: #333;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.detail-button:hover {
  background-color: #e6e6e6;
}

.card {
  width: 80%;
  padding: 15px;
  margin: 15px;
  margin-top: 0;
  background-color: #f2f2f2;
  border-radius: 4px;
 
}

.card-content {
  display: flex;
  flex-direction: column;
}

.info-row {
  display: flex;
  margin-bottom: 8px;
}

.info-label {
  font-weight: bold;
  margin-right: 8px;
}

.info-value {
  flex-grow: 1;
}

.lower-container {
    display: flex;
    flex-direction: column;
    width: 90%;
    margin-top: 0px;
    padding-bottom: 10px;
    justify-content: space-between;
    align-items: center;
    overflow-x: scroll;
  }

  .tab-selector {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  width: 90%;
}

.tab-selector input[type="text"] {
  padding: 8px;
  flex: 1;
  margin-right: 10%;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
}

.tab-selector input[type="text"]:focus {
  outline: none;
  border-color: #999;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);
}

.tab-selector button {
  padding: 10px 20px;
  background-color: #f2f2f2;
  color: #333;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.tab-selector button.active {
  background-color: #e6e6e6;
}

  .user-list-container{
    position: relative;
    height: 50vh;
    overflow-y: scroll;
    width: 100%;
  }

  .button-container {
    display: flex;
    gap: 10px;
  }

  

  

  /*.buttons {
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }

  .add-user-button {
    padding: 8px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-weight: bold;
    transition: background-color 0.3s ease;
  }

  .add-user-button:hover{
    background-color: #0056b3;
  }*/


  .user-table {
    table-layout: auto;
    margin-top: 0px;
    margin-left: auto;
    margin-right: auto;
    border-collapse: collapse;
    width: 100%;
    min-width: 800px;
    overflow-x: scroll;
}

.table-heading {
  text-align: center;
  white-space: nowrap;
  
}

.table-entry {
    text-align: center;
    padding: 16px;
    
  }

table td {
  text-align: center;
  padding: 12px;
  
}
.clickable-row {
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  .clickable-row:hover {
    background-color: lightgray;
  }

.sticky-header {
    position: sticky;
    top: 0;
    background-color: #f5f5f5;
  }

  .popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
}

.popup2 {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
}

.popup-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 20px;
  border-radius: 5px;
}



    @media (max-width: 768px) {
        .container {
            max-width: 400px;
            padding: 30px;
        }
    }
</style>
