export function uploadFile(file, userId, update) {
  return new Promise((resolve, reject) => {
    const formData = new FormData();
    formData.append('file', file);
    formData.append('user_id', userId);
    if (update) {
      formData.append('update', 'true');
    }
    fetch('https://inhealion.gr/program-handler.php', {
      method: 'POST',
      body: formData
    })
      .then(response => {
        if (response.ok) {
          console.log("File uploaded successfully");
          resolve(); // Resolve the Promise without any value
          alert('File uploaded successfully');
        } else if (response.status === 409) {
          alert('Duplicate file error. File with the same name already exists.');
          reject(new Error('Duplicate file error'));
        } else if (response.status === 401) {
          alert('Unauthorized. Please log in to access this functionality.');
          reject(new Error('Unauthorized error'));
        } else {
          alert('Failed to upload file. Internal server error occurred.');
          reject(new Error('Failed to upload file'));
        }
      })
      .catch(error => {
        console.error('An error occurred:', error);
        alert('An error occurred. Please try again later.');
        reject(error); // Reject the Promise with the error
      });
  });
}


