<script>
  import { subscribe } from 'svelte/internal';
  import { allUsers, currentUser, filteredProgArray, profProgs } from '../stores.js';
  import { currentProfile } from '../stores.js';
  import { filteredUserArray } from '../stores.js';
  import { fetchProfProgsById } from '../fileDbHandling.js';
  import { fetchUsersByProf } from '../fileDbHandling.js';
  import { addingStatus } from '../stores.js';
  import { altProfile } from '../stores.js';
  let toggle = false;
  function toggleDetails (){
      toggle = !toggle;
  }

  let userArray = [{id: 1, name: 'user-1'},{id: 1, name: 'user-1'},{id: 1, name: 'user-1'},{id: 1, name: 'user-1'},];
  let ProgArray = [{id: 1, name: 'user-1'},{id: 1, name: 'user-1'},{id: 1, name: 'user-1'},{id: 1, name: 'user-1'},];
  let myId = 0;
  let isUserOpen = false;
  let isProgramOpen = false;
  let activeTab = 'users';
  let searchTerm = "";


 
 
 
  const unsubProfile = currentProfile.subscribe(prof => {
    myId = prof.user_id;
  });
  unsubProfile();
  getData();
  
  
  async function getData(){
    await fetchUsersByProf(myId);
    await fetchProfProgsById(myId);
    const unsubscribeUsers = allUsers.subscribe(users => {
      userArray = [...users];
      filteredUserArray.set(userArray);
      filteredUserArray.subscribe(files => {
        
      })
    });
    const unsubscribeFiles = profProgs.subscribe(files => {
      ProgArray = [...files];
      filteredProgArray.set(ProgArray);
    });
    unsubscribeFiles();
    unsubscribeUsers();
    
  }
  function toggleViewUser(){
    isUserOpen = !isUserOpen;
  }
  function getProfile(id){
    toggleViewUser();
  }
  function toggleViewProgram(){
    isProgramOpen = !isProgramOpen;
  }
  function getProgram(id){
    toggleViewProgram();
  }

  function handleSearch(event) {
  
  searchTerm = event.target.value.toLowerCase();
  //console.log(searchTerm);
  filteredUserArray.set(
    userArray.filter(user => {
      return (
        user.name.toLowerCase().includes(searchTerm) ||
        user.surname.toLowerCase().includes(searchTerm) ||
        user.email.toLowerCase().includes(searchTerm) ||
        user.phone_number.toLowerCase().includes(searchTerm) ||
        user.date_of_birth.toLowerCase().includes(searchTerm)
      );
    })
  );

  filteredProgArray.set(
    ProgArray.filter(program => {
      return (
        program.name.toLowerCase().includes(searchTerm)
                
      );
    })
  );
}
function switchTab(tab) {
  activeTab = tab;
}

function openUser(id) {
  console.log(id);
  addingStatus.set(4);
  altProfile.set(findUserById(id));
}

function findUserById(id) {
  return userArray.find(user => user.id === id) || null;
}


 

</script>

<main>
  
    <div class="user-details">
      <div class="user-info">
        <h3 class="username">{$currentProfile.name} {$currentProfile.surname}</h3>
        <p class="user-role">Professional</p>
        <button class="detail-button" on:click={toggleDetails}>Details</button>
      </div>
      <div>
        <img src="avatar.png" alt="User Avatar" class="avatar" />
      </div>
    </div>

    {#if toggle}
      <div class="card">
        <div class="card-content">
          <div class="info-row">
            <div class="info-label">Email Address:</div>
            <div class="info-value">{$currentProfile.email}</div>
          </div>
          <div class="info-row">
            <div class="info-label">Date of Birth:</div>
            <div class="info-value">{$currentProfile.date_of_birth}</div>
          </div>
          <div class="info-row">
            <div class="info-label">Phone Number:</div>
            <div class="info-value">{$currentProfile.phone_number}</div>
          </div>
          <div class="info-row">
            <div class="info-label">Address:</div>
            <div class="info-value">{$currentProfile.address}</div>
          </div>
        </div>
      </div>
    {/if}

    <div class="lower-container">
    
      
      <div class="tab-selector">
        <input type="text" id="searchInput" placeholder="Search..." on:input={handleSearch} />
          <button class="{activeTab === 'users' ? 'active' : ''}" on:click={() => switchTab('users')}>Users</button>
          <button class="{activeTab === 'programs' ? 'active' : ''}" on:click={() => switchTab('programs')}>Programs</button>
      </div>

      {#if activeTab === 'users'}
      <div class="user-list-container">
        <div class="file-list">
          
          <table class="user-table">
            <!-- Table header -->
            <thead class="sticky-header">
              <tr>
                <th class="table-heading">Name</th>
                <th class="table-heading">Email</th>
                <th class="table-heading">Phone Number</th>
                <th class="table-heading">Date of Birth</th>
              </tr>
            </thead>
      
            <!-- Table body -->
            <tbody>
              {#each $filteredUserArray as user}
              <tr class="clickable-row" on:click={() => openUser(user.id)}>
                <td class="table-entry">{user.name} {user.surname}</td>
                <td class="table-entry">{user.email}</td>
                <td class="table-entry">{user.phone_number}</td>
                <td class="table-entry">{user.date_of_birth}</td>
              </tr>
              {/each}
            </tbody>
          </table>
        </div>
      </div>
      {/if}

      {#if activeTab === 'programs'}
      <div class="user-list-container">
        <div class="file-list">
          <table class="user-table">
            <thead class="sticky-header">
              <tr>
                <th class="table-heading">Name</th>
                <th class="table-heading">Created</th>
                <th class="table-heading">Last Updated</th>
              </tr>
            </thead>
            <tbody>
              {#each $filteredProgArray as program}
              <tr class="clickable-row">
                <td class="table-entry">{program.name}</td>
                <td class="table-entry">{program.created_at}</td>
                <td class="table-entry">{program.updated_at}</td>
              </tr>
              {/each}
            </tbody>
          </table>
        </div>
      </div>
      
      {/if}
  </div>

    
  </main>
   
   
      

    


<style>
  main {
    padding: 0px;
    margin: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%
    
  }

  
  .user-details {
    display: flex;
    width: 90%;
    padding: 10px;
    flex-direction: row;
    align-items: center;
    margin-bottom: 10px;
    justify-content: space-between;
    padding: 10px;
  }

  .username {
    font-size: 24px;
    margin-bottom: 2px;
  }

  .user-role {
    font-size: 18px;
    color: #555;
    margin: 3px 0px;
  }

  .avatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
  }

  .detail-button {
  padding: 5px;
  background-color: #f2f2f2;
  color: #333;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.detail-button:hover {
  background-color: #e6e6e6;
}


  

  .card {
  width: 80%;
  padding: 15px;
  margin: 15px;
  margin-top: 0;
  background-color: #f2f2f2;
  border-radius: 4px;
 
}

.card-content {
  display: flex;
  flex-direction: column;
}

.info-row {
  display: flex;
  margin-bottom: 8px;
}

.info-label {
  font-weight: bold;
  margin-right: 8px;
}

.info-value {
  flex-grow: 1;
}


.lower-container {
    display: flex;
    flex-direction: column;
    width: 90%;
    margin-top: 0px;
    padding-bottom: 10px;
    justify-content: space-between;
    align-items: center;
    overflow-x: scroll;
  }

  .tab-selector {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  width: 90%;
}

.tab-selector input[type="text"] {
  padding: 8px;
  flex: 1;
  margin-right: 10%;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
}

.tab-selector input[type="text"]:focus {
  outline: none;
  border-color: #999;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);
}

.tab-selector button {
  padding: 10px 20px;
  background-color: #f2f2f2;
  color: #333;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.tab-selector button.active {
  background-color: #e6e6e6;
}

  .user-list-container{
    position: relative;
    height: 50vh;
    overflow-y: scroll;
    width: 100%;
  }

  
  .user-table {
    table-layout: auto;
    margin-top: 0px;
    margin-left: auto;
    margin-right: auto;
    border-collapse: collapse;
    width: 100%;
    min-width: 800px;
    overflow-x: scroll;
}

.table-heading {
  text-align: center;
  white-space: nowrap;
  
}

.table-entry {
    text-align: center;
    padding: 16px;
    
  }

table td {
  text-align: center;
  padding: 12px;
  
}
.clickable-row {
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  .clickable-row:hover {
    background-color: lightgray;
  }

.sticky-header {
    position: sticky;
    top: 0;
    background-color: #f5f5f5;
  }



</style>
