export async function fetchFolders(user_id) {
  return new Promise(async (resolve) => {
    
    let folders = [];
    
    try {
      const formData = new URLSearchParams();
      
      
      
      formData.append('fetchfoldersbyuserid', user_id);
      //formData.append('pwd', password);
  
      //console.log('cp1');
      const response = await fetch('https://inhealion.gr/api_2.php', {
        method: 'POST',
        body: formData
      });
      
  
      if (response.ok) {
        await response.json().then(res => {
          folders = res;
          files.set(folders);
          resolve(folders);
          //console.log(folders);
  
        });
        //let arr = text;
        //let body = text.body();
        //console.log(arr);
        
      } else {
        const error = await response.text();
        alert(error);
      }
      
    } catch (error) {
      console.error('Database Error:', error);
      alert('An error occurred fetching folders.');
    }
    //console.log(folders);
    
   
   });
}

