export function fetchUsers() {
  return new Promise(async (resolve, reject) => {
    try {
      let usersArray = [];

      const formData = new URLSearchParams();
      formData.append('fetchallusers', null);

      const response = await fetch('https://inhealion.gr/api_2.php', {
        method: 'POST',
        body: formData
      });

      if (response.ok) {
        const res = await response.json();
        usersArray = res;
        //console.log(usersArray);
      } else {
        const error = await response.text();
        throw new Error(error);
      }

      allUsers.set(usersArray);
      

      resolve(true);
    } catch (error) {
      console.error('Database Error:', error);
      alert('An error occurred fetching Users');
      reject(error);
    }
  });
}




