export async function renewFolderExpirationDate(renewDate, folderId) {
  // Create a new FormData object
  const formData = new FormData();
  formData.append('renewdate', renewDate);
  formData.append('folderid', folderId);

  // Send a POST request to the API endpoint
  try {
    const response = await fetch('https://inhealion.gr/api_2.php', {
      method: 'POST',
      body: formData
    });

    if (response.ok) {
      const data = await response.text();
      console.log(data); // Log the response
      // Do additional operations or update the UI as needed
      alert('Folder expiration date renewed successfully.');
    } else {
      throw new Error(response.statusText);
    }
  } catch (error) {
    console.error('Error:', error);
    // Handle the error case
    alert('Error updating folder expiration date. Please try again later.');
  }
}

export function updateProfessional(user_id, professional_id) {
  return new Promise((resolve, reject) => {
    const url = 'https://inhealion.gr/api_2.php';
    const formData = new FormData();
    formData.append('user_id', user_id);
    formData.append('addprofessional_id', professional_id);

    fetch(url, {
      method: 'POST',
      body: formData
    })
      .then(response => {
        if (response.ok) {
          // Resolve the promise if response code is 200
          return response.text();
        } else if (response.status === 404) {
          // Reject with specific error message if response code is 404
          throw new Error('User not found');
        } else if (response.status === 500) {
          // Reject with specific error message if response code is 500
          throw new Error('Internal server error');
        } else {
          // Reject with generic error message for other response codes
          throw new Error('Unknown error occurred');
        }
      })
      .then(resolve)
      .catch(error => {
        console.error('Error:', error);
        // Reject with the error object for any network errors or exceptions
        reject(error);
      });
  });
}










