<script>
  import { subscribe } from 'svelte/internal';
  import { currentUser } from '../stores.js';
  import { currentProfile } from '../stores.js';
  import { fetchFolders } from '../fileDbHandling.js';
  import { fetchPrograms } from '../fileDbHandling.js';
  import { logout } from '../loginService.js';
  import { files } from '../stores.js';
  import { programs } from '../stores.js';
  

  
  
  let folders = [];
  let programArray = [];
  let isProgramList = false;
  let toggle = false;
  let fwUpdate = false;
  let programUpdate = true;
  fetchFileHistory();


  async function fetchFileHistory(){
    let user_id = 0;
    const unsubscribe = currentUser.subscribe(value => {
        //console.log(value);
        user_id = value.user_id;
      });
      unsubscribe();
     
    await fetchFolders(user_id);
    unsubscribeFiles();
   // console.log('folders');
   // console.log(folders);
  }

  async function getFiles(item){
    
    
    await fetchPrograms(item);
   
    toggleView();
  }
  const unsubscribeFiles = files.subscribe(filenames =>{
    folders = [...filenames];
  }
  )

  const unsubscribePrograms = programs.subscribe(filenames =>{
    programArray = [...filenames];
  }
  )

  function onLogout() {
    
    logout();
  }
  function toggleDetails (){
      toggle = !toggle;
  }
  function toggleView() {
    isProgramList = !isProgramList;
  }
  function handleKeyDown(event) {
    if (event.key === "Enter" || event.key === " ") {
      // Call the toggleView function when Enter or Space key is pressed
      toggleView();
    }
  }

  function calculateExpiryDate(seconds, date) {
  //console.log(date);
  const parsedDate = new Date(date);

  if (isNaN(parsedDate) || isNaN(seconds)) {
    //console.log("notdate");
    return null; // Return null or handle the error in an appropriate way
  }

  const newDate = new Date(parsedDate.getTime() + seconds * 1000);
  const formattedDate = newDate.toISOString().split("T")[0];

  return formattedDate;
}

function isDateExpired(expiryDate) {
  const currentDate = new Date();
  const expiryDateObj = new Date(expiryDate);
  return expiryDateObj < currentDate;
}
</script>

<main>
  
    <div class="user-details">
      <div class="user-info">
        <h3 class="username">{$currentProfile.name} {$currentProfile.surname}</h3>
        <p class="user-role">User</p>
        <button class="detail-button" on:click={toggleDetails}>Details</button>
      </div>
      <div>
        <img src="avatar.png" alt="User Avatar" class="avatar" />
      </div>
    </div>

    {#if toggle}
      <div class="card">
        <div class="card-content">
          <div class="info-row">
            <div class="info-label">Email Address:</div>
            <div class="info-value">{$currentProfile.email}</div>
          </div>
          <div class="info-row">
            <div class="info-label">Date of Birth:</div>
            <div class="info-value">{$currentProfile.date_of_birth}</div>
          </div>
          <div class="info-row">
            <div class="info-label">Phone Number:</div>
            <div class="info-value">{$currentProfile.phone_number}</div>
          </div>
          <div class="info-row">
            <div class="info-label">Address:</div>
            <div class="info-value">{$currentProfile.address}</div>
          </div>
        </div>
      </div>
    {/if}



    <div class="lower-container">
      {#if !isProgramList}
      <div class="user-list-container">
        <div class="file-list">
          <table class="user-table">
            <thead class="sticky-header"> 
              <tr>
                <th class="table-heading">Name</th>
                <th class="table-heading">Created</th>
                <th class="table-heading">Expiry</th>
              </tr>
            </thead>
            <tbody>
              {#each folders as folder}
              <tr class="clickable-row" on:click={getFiles(folder.id)}>
                <td class="table-entry">{folder.name}</td> 
                <td class="table-entry">{folder.created_at}</td>             
                
                {#if isDateExpired(calculateExpiryDate(folder.expires_in, folder.created_at))}
                  <td class="table-entry expired">
                    {calculateExpiryDate(folder.expires_in, folder.created_at)}
                  </td>
                {:else}
                  <td class="table-entry">
                    {calculateExpiryDate(folder.expires_in, folder.created_at)}
                  </td>
                {/if}

              </tr>
              {/each}
            </tbody>
          </table>
          
        </div>
      </div>
      {/if}

      {#if isProgramList}
      <button class="back-button" on:click={toggleView}>Go Back</button>
      <div class="user-list-container">
        <div class="file-list">
          <table class="user-table">
            <thead class="sticky-header">
              <tr>
                <th class="table-heading">Name</th>
                <th class="table-heading">Created</th>
                <th class="table-heading">Updated</th>
              </tr>
            </thead>
            <tbody>
              {#each programArray as program}
              <tr on:click={toggleView}>
                <td class="table-entry">{program.name}</td>
                <td class="table-entry">{program.created_at}</td>
                <td class="table-entry">{program.updated_at}</td>
              </tr>
              {/each}
            </tbody>
          </table>
          
        </div>
        
      </div>
      
      {/if}
    </div>
    
  
</main>

<style>
  main {
    padding: 0px;
    margin: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%
    
  }

  
  .user-details {
    display: flex;
    width: 90%;
    padding: 10px;
    flex-direction: row;
    align-items: center;
    margin-bottom: 10px;
    justify-content: space-between;
    padding: 10px;
  }

  .username {
    font-size: 24px;
    margin-bottom: 2px;
  }

  .user-role {
    font-size: 18px;
    color: #555;
    margin: 3px 0px;
  }

  .avatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
  }

  .detail-button {
  padding: 5px;
  background-color: #f2f2f2;
  color: #333;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.detail-button:hover {
  background-color: #e6e6e6;
}


  .lower-container {
    display: flex;
    flex-direction: row;
    width: 90%;
    margin-top: 20px;
    padding-bottom: 30px;
    justify-content: space-between;
  }

  
  .file-list {
    width: 90%;
    justify-content: left;
    background-color: #fff; /* Very light gray background color */
    text-align: left;
    padding: 10px;
    padding-top: 0;
    border-radius: 8px;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;
    max-height: 250px;
    /*overflow-y: auto;*/
  }

  .card {
  width: 80%;
  padding: 15px;
  margin: 15px;
  margin-top: 0;
  background-color: #f2f2f2;
  border-radius: 4px;
 
}

.card-content {
  display: flex;
  flex-direction: column;
}

.info-row {
  display: flex;
  margin-bottom: 8px;
}

.info-label {
  font-weight: bold;
  margin-right: 8px;
}

.info-value {
  flex-grow: 1;
}
.user-table {
    table-layout: auto;
    margin-top: 0px;
    margin-left: auto;
    margin-right: auto;
    border-collapse: collapse;
    width: 100%;
    min-width: 500px;
    overflow-x: scroll;
}

.table-heading {
  text-align: center;
  white-space: nowrap;
  
}

.table-entry {
    text-align: center;
    padding: 16px;
    
  }

table td {
  text-align: center;
  padding: 12px;
  
}
.clickable-row {
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  .clickable-row:hover {
    background-color: lightgray;
  }

.sticky-header {
    position: sticky;
    top: 0;
    background-color: #f5f5f5;
  }

  .user-list-container{
    position: relative;
    height: 60vh;
    overflow-y: scroll;
    width: 100%;
  }

  .expired {
    color: red;
  }

  .back-button {
    background-color: blue;
    border: none;
    color: white;
    padding: 10px 20px;
    font-size: 16px;
  }

  .back-button::before {
    content: '\2190'; /* Unicode character for left arrow */
    margin-right: 5px;
  }

</style>
